* {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}
body #root{
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: url(/src/img/bg.png) no-repeat;
  background-size: cover;
  padding: 0 5%;

}
body #root header{
  width: 100%;
  padding: 20px;

}
body #root header .container{
  display: flex;
  align-content: center;
  justify-content: space-between;
}
body #root header .logo{
  height: 100px;
  margin-left: 5%;
  margin-top: 0;
}
body #root .content{
  height: 100vh;
  width: 100%;
  gap: 3%;
  display: flex;
  /* align-content: stretch;
  align-items: stretch;
  justify-content: flex-start; */
}


body #root .content .sideBar{
  padding: 20px 20px 0;
  flex-grow: 5;
}
body #root .content .inventory{

  padding: 20px;
  flex-grow: 5;
}

body #root .content .inventory .title{
  width: 20%;
}

body #root .content .inventory .inventory_content img{
  height: 70px;
  margin-bottom: -20px;
}

body #root .content .inventory .inventory_content .inventory_item{
  background: url(./img/divider.png) no-repeat;
  background-size: contain;
   height: 120px;
   margin-bottom: 20px;
   display: flex;
   justify-content: center;
   width: 60%;
}


body #root .content .inventory .inventory_content .inventory_item .chest{
  transform: scale(2);
  position: relative;
  top: -30px;
  z-index: 2;
}
body #root .content .inventory .inventory_content .inventory_item .chest_item{
  position: relative;
}
body #root .content .inventory .inventory_content .inventory_item .chest_item .counter{
  background: #fff;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  position: absolute;
  top: -80px;
  right: -70px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 5;
  font-size: 20px;
}

/* body #root .content .inventory h3{
  font
} */

body #root .content .sideBar .side_item{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 20px 0;
}
body #root .content .sideBar .side_item button{
  margin: 10px 20px 0;
  background: transparent;
  border: none;
  cursor: pointer;

}
body #root .content .sideBar .side_item img{
  height: auto;
  width: 150px;
}



/* popup */
.popup{
  position: fixed;
  top: 20%;
  left : 40%;
  background: rgba(255,255,255);
  z-index: 30;
  width: 400px;
  padding: 40px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  display: none;
}
.popup ul{
  list-style: none;
  font-size: 26px;
}
.popup button{
  appearance: none;
  border: none;
  background: transparent;
  cursor: pointer;
  margin-top: 20px;
  width: 80%;
}
.popup img{
  width: 100%;
}

.popupbg{
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0,0,0,0.6);
  z-index: 29;
}